:root
{
  --yellow: #F5C32C;
    --orange : #FCA61F;
    --black : #242D49;
    --gray : #788097;
    --blueCard : #DDF8FE;
    --purple: rgb(238 210 255);
    --boxShadow : 0px 19px 60px rgb(0 0 0 / 8%);
    --orangeCard: rgba(252, 166, 31, 0.45);
    --smboxShadow: -79px 51px 60px rgba(0, 0, 0, 0.08);
}

.App{
    padding: 0.5rem 3.5rem;
    overflow: hidden;
    color: var(--black);    
}

.button{
  border-radius:34px;
  border: none;
  color:white;
  font-size:16px;
  padding: 7px 26px 7px 26px;
  background: #080879;
  box-shadow: #F5C32C;
}
.button:hover{
  cursor: pointer;
  background-color:white;
  color:blue;
  border: 1px solid blue;
}


  

  